

import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { map, catchError } from 'rxjs/operators';
import { API_URL } from '../config/config';
import { User } from '../models/user.model';
import { Observable } from 'rxjs';
import { AuthService } from './auth.service';
import { Contest } from '../models/contest.model';
import * as moment from 'moment';

@Injectable({
  providedIn: 'root'
})
export class ContestsService {

  constructor(private http: HttpClient, private authService: AuthService) { }

  contestsUrl = API_URL + 'api/contest';

  getContests(): Observable<any> {
    let usertToken = this.authService.getToken();
    let headers = new HttpHeaders({ 'Content-Type': 'application/json', 'Accept': 'application/json, text/plain', 'Authorization': 'Bearer '+ usertToken});
    return this.http.get<any>(this.contestsUrl, {'headers': headers});
  }

  getContest(contestId: number): Observable<any> {
    let usertToken = this.authService.getToken();
    let headers = new HttpHeaders({ 'Content-Type': 'application/json', 'Accept': 'application/json, text/plain', 'Authorization': 'Bearer '+ usertToken});
    return this.http.get<any>(this.contestsUrl+"/"+contestId, {'headers': headers});
  }

  getImage(path: string): Observable<any> {
    const heads = new HttpHeaders({
      'Content-Type': 'application/json',
      Accept: 'application/json'
    });
    return this.http.get(path, { headers: heads, responseType: 'blob' as 'json'});
  }

  postPutContest(data: Contest, method: string): Promise<any> {
    let url = this.contestsUrl;
    let file = data.image_path;
    if (method === 'PUT') {
      url = `${url}/${data.id}`;
      file = new File([data.image_file], `${data.name}.${data.image_file.type.split('/')[1]}`, {
        type: data.image_file.type
      });
    }
    return new Promise((resolve, reject) => {
      const formData = new FormData();
      const xhr = new XMLHttpRequest();
      if (data.id) {
        formData.append('user', data.user.toString());
      }
      formData.append('image_path', file, file.name);
      formData.append('name', data.name);
      formData.append('url', data.url);
      data.begin_at = moment(moment(data.begin_at).set({second: 30})).toDate();
      data.end_at = moment(moment(data.end_at).set({second: 30})).toDate();
      formData.append('begin_at', moment(data.begin_at).format('YYYY-MM-DD HH:MM:SS'));
      formData.append('end_at', moment(data.end_at).format('YYYY-MM-DD HH:MM:SS'));
      formData.append('description', data.description);
      xhr.onreadystatechange = () => {
        if ( xhr.readyState === 4) {
          if ( xhr.status === 201 || xhr.status === 200 ) {
            resolve(JSON.parse(xhr.response));
          } else {
            reject(xhr.response);
          }
        }
      };
      xhr.open(method, url, true);
      xhr.setRequestHeader('Authorization', 'Bearer ' + this.authService.getToken());
      xhr.send(formData);
    });
  }

  deleteContest(id: number): Observable<any> {
    const url = this.contestsUrl + '/' + id;
    const httpOptions = {
      headers: new HttpHeaders({
        Authorization: 'Bearer ' + this.authService.getToken()
      })
    };
    return this.http.delete(url, httpOptions).pipe(
      map( res => res ),
      catchError(err => err)
    );
  }

}
