import { Component, OnInit } from '@angular/core';
import { FormGroup, FormControl, Validators } from '@angular/forms';
import { User } from '../../core/models/user.model';
import { GeneralService } from '../../core/services/general.service';
import { AuthService } from '../../core/services/auth.service';
// import Swal from 'sweetalert2';
import { Router } from '@angular/router';
import { error } from 'protractor';


@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.scss']
})
export class HomeComponent implements OnInit {

  isLogin = true;
  isRegister = false;
  showMsg: boolean;
  sev: string;
  msg: string;
  accessForm: FormGroup;
  isEmailError: boolean;
  isPassError: boolean;

  constructor(private generalService: GeneralService, private authService: AuthService,
    public router: Router) { }

  ngOnInit() {
    this.setLoginForm();
  }

  setLoginForm(): void {
    this.isLogin = true;
    this.isRegister = false;
    this.accessForm = new FormGroup({
      email: new FormControl(null, Validators.required),
      password: new FormControl(null, Validators.required),
    });
  }

  setRegisterForm(): void {
    this.isLogin = false;
    this.isRegister = true;
    this.accessForm = new FormGroup({
      first_name: new FormControl(null, Validators.required),
      last_name: new FormControl(null, Validators.required),
      email: new FormControl(null, Validators.required),
      password: new FormControl(null, Validators.required),
      passwordConf: new FormControl(null, Validators.required),
    });
  }

  loginUser(): void {
    const user: User = this.accessForm.value;
    this.authService.login(user).subscribe(
      result => {
        console.log(result);
      },
      error => {
        console.log(error);
        this.showMessage('error','Datos de Login no son validos')
      },
      () => {
         this.router.navigate(['/contests']);
       }
    );
  }

  registerUser(): void {
    delete this.accessForm.value.passwordConf;
    const user: User = this.accessForm.value;
    this.generalService.register(user).subscribe((resp: any) => {
      this.isRegister = false;
      this.isLogin = true;
      this.accessForm.patchValue({email: user.email, password: user.password});
    },
    (err) => {
      if (err.error.email) {
        this.isEmailError = true;
        setTimeout(() => this.isEmailError = false, 6000);
      } else if (err.error.password) {
        this.isPassError = true;
        setTimeout(() => this.isPassError = false, 6000);
      } else {
        this.showMessage('error', 'Error creando el usuario!');
      }
    });
  }

  private showMessage(severity: string, message: string) {
    this.showMsg = true;
    this.sev = severity;
    this.msg = message;
    setTimeout(() => this.showMsg = false, 5000);
  }

}
