

import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { map, catchError } from 'rxjs/operators';
import { API_URL } from '../config/config';
import { Observable } from 'rxjs';
import { AuthService } from './auth.service';
import { Video } from '../models/video.model';

@Injectable({
  providedIn: 'root'
})
export class VideosService {

  constructor(private http: HttpClient, private authService: AuthService) { }

  videosForContestsUrl = API_URL + 'public/contest/';
  videosForContestsWithAuthUrl = API_URL + 'api/videoContest/';
  videoUrl = API_URL + 'api/video';


  getContestVideos(url: string): Observable<any> {
    const usertToken = this.authService.getToken();
    if(usertToken){
      let headers = new HttpHeaders({ 'Content-Type': 'application/json', 'Accept': 'application/json, text/plain', 'Authorization': 'Bearer '+ usertToken});
      return this.http.get<any>(this.videosForContestsWithAuthUrl+url, {'headers': headers});

    }else{
      return this.http.get<any>(this.videosForContestsUrl+url);
    }
  }

  getVideo(videoId: number): Observable<any> {
    const usertToken = this.authService.getToken();
    let headers = new HttpHeaders({ 'Content-Type': 'application/json', 'Accept': 'application/json, text/plain', 'Authorization': 'Bearer '+ usertToken});
    return this.http.get<any>(this.videoUrl+"/"+videoId, {'headers': headers});
  }

  getVideoFile(filename: string): Observable<Blob> {
    const url = API_URL.slice(0, -1) + filename;
    const heads = new HttpHeaders({
      'Content-Type': 'application/json',
      Accept: 'application/json'
    });
    return this.http.get<Blob>(url, { headers: heads, responseType: 'blob' as 'json'});
  }

  // tslint:disable-next-line:adjacent-overload-signatures
  postVideo(data: Video): Promise<any> {
    const url = this.videoUrl;
    return new Promise((resolve, reject) => {
      const formData = new FormData();
      const xhr = new XMLHttpRequest();
      formData.append('file', data.file, data.file.name);
      formData.append('user_name', data.user_name);
      formData.append('last_name', data.last_name);
      formData.append('email', data.email);
      formData.append('contest', data.contest.toString());
      formData.append('comment', data.comment);
      xhr.onreadystatechange = () => {
        if ( xhr.readyState === 4) {
          if ( xhr.status === 201 || xhr.status === 200 ) {
            resolve(JSON.parse(xhr.response));
          } else {
            reject(JSON.parse(xhr.response));
          }
        }
      };
      xhr.open('POST', url, true);
      // xhr.setRequestHeader('Authorization', 'Bearer ' + this.authService.getToken());
      xhr.send(formData);
    });
  }

}
