import { Component, OnInit } from '@angular/core';
import { FormGroup, FormControl, Validators } from '@angular/forms';
import { VideosService } from 'src/app/core/services/videos.service';
import { ActivatedRoute } from '@angular/router';
import { Video } from 'src/app/core/models/video.model';
import { SelectItem } from 'primeng/api/selectitem';
import {ConfirmationService} from 'primeng/api';
import { API_URL } from 'src/app/core/config/config';
import { AuthService } from 'src/app/core/services/auth.service';
declare var jwplayer: any;

@Component({
  selector: 'app-videos',
  templateUrl: './videos.component.html',
  styleUrls: ['./videos.component.scss'],
  providers: [ConfirmationService]
})
export class VideosComponent implements OnInit {

  videos: Video[];
  url: string;
  selectedVideo: Video;
  displayDialog: boolean;
  sortOptions: SelectItem[];
  sortKey: string;
  sortField: string;
  sortOrder: number;
  formDialog: boolean;
  videoForm: FormGroup;

  avi: string;

  constructor(
    private videosService: VideosService,
    private route: ActivatedRoute,
    private confirmationService: ConfirmationService,
    private authService: AuthService,
  ) {
    this.route.params.subscribe(params => this.url = params.url);
  }

  ngOnInit() {
    this.videosService.getContestVideos(this.url).subscribe(videos => {
      if (!videos.length) {
        return;
      }
      this.videos = videos;
      setTimeout(() => videos.forEach((v: any) => this.setVideoViewer(`video${v.id}`, v.file, 280, 150)), 200);
    });
    this.sortOptions = [
      { label: 'Mas reciente', value: '!begin_at' },
      { label: 'Mas antiguo', value: 'begin_at' }
    ];
  }

  selectVideo(event: Event, video: Video) {
    this.selectedVideo = video;
    this.displayDialog = true;
    event.preventDefault();
    this.avi = API_URL + video.file;
    setTimeout(() => {
      this.setVideoViewer('videoPending', video.file, 400, 220);
      this.setVideoViewer('videoReady', video.format_set[0].file, 400, 220);
    }, 200);
  }

  setVideoViewer(divId: string, url: string, width: number, height: number): void {
    const playerJw = jwplayer(divId).setup({
      title: 'Player Test',
      file: `${API_URL}${url}`,
      width,
      height,
      aspectratio: '16:9',
      mute: false,
      autostart: false,
      primary: 'html5',
    });
  }

  onSortChange(event) {
    let value = event.value;
    if (value.indexOf('!') === 0) {
      this.sortOrder = -1;
      this.sortField = value.substring(1, value.length);
    } else {
      this.sortOrder = 1;
      this.sortField = value;
    }
  }

  async openForm(v?: Video) {
    this.formDialog = true;
    this.videoForm = new FormGroup({
        // id: new FormControl(v ? v.id : null),
        contest: new FormControl(this.url),
        user_name: new FormControl(null, Validators.required),
        last_name: new FormControl(null, Validators.required),
        email: new FormControl(null, Validators.required),
        comment: new FormControl(null, Validators.required),
        file: new FormControl(null, Validators.required),
        status: new FormControl(null),
    });
  }

  onVideoSelect(e: any) {
    const file = e.files[0];
    this.videoForm.patchValue({file});
  }

  saveVideo(video: Video) {
    this.displayDialog = false;
    this.formDialog = false;
    this.videosService.postVideo(video).then((res) => {
      if (this.authService.getToken()) {
        if (this.videos && this.videos.length) {
          this.videos.push(res);
        }
        setTimeout(() => {
          this.setVideoViewer(`video${res.id}`, res.url, 280, 150);
        }, 300);
      }
      this.confirmationService.confirm({
        message: `Hemos recibido tu video y lo estamos procesando para que sea publicado.
          Tan pronto el video quede publicado en la pgina del concurso te notificaremos por email.`,
        header: 'Anuncio',
        accept: () => null,
        reject: () => null
      });
    });
  }

}
