import { Injectable } from '@angular/core';
import { CanActivate, Router } from '@angular/router';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Observable, throwError } from 'rxjs';
import { retry, catchError, map } from 'rxjs/operators';
import { API_URL } from '../config/config';
import { User } from '../models/user.model';



@Injectable({
  providedIn: 'root'
})
export class AuthService implements CanActivate {

  authUrl = `${API_URL}auth/jwt/create/`;
  userToken = null;

  // Http Headers
  httpOptions = {
    headers: new HttpHeaders({
      'Content-Type': 'application/json'
    })
  };

  constructor(private router: Router, public http: HttpClient) {
    console.log('se llamo el servicio');
  }
  
  login(usuario: User): Observable<string> {
    return this.http.post(this.authUrl, JSON.stringify(usuario), this.httpOptions)
      .pipe(
        map((response: Response) => {
          this.userToken = response.access;
          this.storage(this.userToken);
          return this.userToken;
        }),
        retry(1),
        catchError(err => {
          console.log('Error en el login', err);
          return throwError(err);
        }
        )
      );
  }

  getData(): string {
    return this.userToken;
  }

  getToken(): string {
    if (sessionStorage.getItem('userToken')) {
      this.userToken = JSON.parse(sessionStorage.getItem('userToken'));
    } else {
      this.userToken = null;
    }
    return this.userToken;
  }


  private storage(token: string) {
    sessionStorage.setItem('userToken', JSON.stringify(token));
  }

  canActivate(): boolean {
    if (!this.userToken) {
      this.userToken = this.getToken();
    }
    const signedIn = !!this.userToken;
    console.log("signedIn: ", signedIn);
    if (!signedIn) {
      this.router.navigateByUrl('/login');
    }
    return signedIn;
  }
}

interface Response {
  access: string;
}
