import { Component, OnInit } from '@angular/core';
import {DomSanitizer} from '@angular/platform-browser';
import { FormGroup, FormControl, Validators } from '@angular/forms';
import { ContestsService } from 'src/app/core/services/contests.service';
import { Contest } from 'src/app/core/models/contest.model';
import { SelectItem } from 'primeng/api/selectitem';
import { Router } from '@angular/router';
import { API_URL } from '../../core/config/config';
import {ConfirmationService} from 'primeng/api';

@Component({
  selector: 'app-contests',
  templateUrl: './contests.component.html',
  styleUrls: ['./contests.component.scss'],
  providers: [ConfirmationService]
})
export class ContestsComponent implements OnInit {

  constructor(
    private contestsService: ContestsService,
    private router: Router,
    private sanitizer: DomSanitizer,
    private confirmationService: ConfirmationService
  ) { }

  contests: Contest[];
  sortOptions: SelectItem[];
  sortKey: string;
  sortField: string;
  sortOrder: number;
  isForm: boolean;
  contestForm: FormGroup;
  img: any;
  isInfDialog = true;
  isDelDialog = false;
  showUrlError = false;

  ngOnInit() {
    this.loadContest();
    this.sortOptions = [
      {label: 'Mas reciente', value: '!begin_at'},
      {label: 'Mas antiguo', value: 'begin_at'}
  ];
  }

  loadContest(): void {
    this.contestsService.getContests().subscribe( async contests => {
      contests.forEach(c => {
        c.showImage = false;
        this.contestsService.getImage(API_URL.slice(0, -1) + c.image_path).subscribe(img => {
          const reader = new FileReader();
          reader.addEventListener('load', () => {
            c.image_path = reader.result;
            c.showImage = true;
          }, false);
          if (img) {
            reader.readAsDataURL(img);
          }
          c.image_path = this.sanitizer.bypassSecurityTrustUrl(img.type);
          c.image_file = img;
        });
      });
      this.contests = contests;
    });
  }

  selectContest(event: Event, contest: Contest) {
    this.router.navigate(['/' + contest.url],
      {state: {contest}}
    );
  }

  onSortChange(event) {
        let value = event.value;

        if (value.indexOf('!') === 0) {
            this.sortOrder = -1;
            this.sortField = value.substring(1, value.length);
        } else {
            this.sortOrder = 1;
            this.sortField = value;
        }
  }

  async openForm(c?: Contest) {
    this.img = c ? c.image_path : null;
    this.isForm = true;
    this.contestForm = new FormGroup({
        id: new FormControl(c ? c.id : null),
        user: new FormControl(c ? c.user : null),
        name: new FormControl(c ? c.name : null, Validators.required),
        url: new FormControl(c ? c.url : null, Validators.required),
        begin_at: new FormControl(c ? new Date(c.begin_at) : null, Validators.required),
        end_at: new FormControl(c ? new Date(c.end_at) : null, Validators.required),
        image_path: new FormControl(c ? c.image_path : null, Validators.required),
        image_file: new FormControl(c ? c.image_file : null),
        description: new FormControl(c ? c.description : null, Validators.required),
    });
  }

  onImageSelect(e: any) {
    const file = e.files[0];
    this.contestForm.patchValue({image_path: file});
    this.contestForm.patchValue({image_file: file});
    const reader = new FileReader();
    reader.readAsDataURL(file);
    reader.onload = () => this.img = reader.result;
  }

  saveContest(contest: Contest): void {
    const method = contest.id ? 'PUT' : 'POST';
    this.contestsService.postPutContest(contest, method).then(res => {
      this.isForm = false;
      this.loadContest();
      this.showDialog('Informacin registrada exitosamente', 'Anuncio', true);
    }, err => {
      this.showUrlError = true;
      setTimeout(() => this.showUrlError = false, 6000);
    });
  }

  confirmDeleteDialog(contest: Contest) {
    this.showDialog('Est seguro de eliminar el concurso?', 'Confirmacin', false, this.deleteContest, contest);
  }

  deleteContest(that: any, contest: Contest) {
    that.contestsService.deleteContest(contest.id).subscribe((res: any) => {
      const index = that.contests.findIndex(c => c.id === contest.id);
      that.contests.splice(index, 1);
    });
  }

  showDialog(msg: string, hdr: string, isInfo: boolean, accept?: any, contest?: Contest) {
    const that = this;
    this.isInfDialog = isInfo ? true : false;
    this.isDelDialog = !this.isInfDialog;
    this.confirmationService.confirm({
      message: msg,
      header: hdr,
      accept: () => accept ? accept(that, contest) : null,
      reject: () => null
    });
  }

}
